                            </container-componente>
                        </p>
                        <p v-if="form.formaPagamento && form.formaPagamento.vista && form.formaPagamento.caixa">
                            <strong>Caixa: </strong> {{form.formaPagamento.caixa.nome}}
                        </p>
                        <div v-if="form.formaPagamento && !form.formaPagamento.vista" class="row">
                            <div class="col-md-3">
                                <strong>Parcelado em 
                                    <select class="form-control" v-model="form.qtde_parcelas" @change="gerarParcelas()">
                                        <option v-if="1 <= form.formaPagamento.parcelas_maximo" :value="1">1x </option>
                                        <option v-if="2 <= form.formaPagamento.parcelas_maximo" :value="2">2x </option>
                                        <option v-if="3 <= form.formaPagamento.parcelas_maximo" :value="3">3x </option>
                                        <option v-if="4 <= form.formaPagamento.parcelas_maximo" :value="4">4x </option>
                                        <option v-if="5 <= form.formaPagamento.parcelas_maximo" :value="5">5x </option>
                                        <option v-if="6 <= form.formaPagamento.parcelas_maximo" :value="6">6x </option>
                                        <option v-if="7 <= form.formaPagamento.parcelas_maximo" :value="7">7x </option>
                                        <option v-if="8 <= form.formaPagamento.parcelas_maximo" :value="8">8x </option>
                                        <option v-if="9 <= form.formaPagamento.parcelas_maximo" :value="9">9x </option>
                                        <option v-if="10 <= form.formaPagamento.parcelas_maximo" :value="10">10x </option>
                                        <option v-if="11 <= form.formaPagamento.parcelas_maximo" :value="11">11x </option>
                                        <option v-if="12 <= form.formaPagamento.parcelas_maximo" :value="12">12x </option>
                                    </select>
                                </strong>
                            </div>
                            <div class="col-md-9">
                                <div class="table-responsive">
                                    <table class="table table-nowrap table-centered mb-0">
                                        <thead>
                                            <tr>
                                                <th scope="col">Parcela</th>
                                                <th scope="col">Data</th>
                                                <th style="width:30%;" scope="col">Valor</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="p in form.parcelas" :key="p.parcela">
                                                <td class="text-center">
                                                    {{p.parcela > 0 ? p.parcela : 'Entrada'}}                                                    
                                                </td>
                                                <td class="text-center">
                                                    <date-picker v-model="p.data_vencimento" :format="'DD/MM/YYYY'" @input="$forceUpdate()" ></date-picker>
                                                </td>
                                                <td class="text-center">
                                                    <strong>{{p.valor | currency($store.state.moeda)}}</strong>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>                            
                        </div>
                        
                        <mensagem-componente titulo="Erro!" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                        <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Voltar</button>                        
                        <button type="submit" class="btn btn-primary" :disabled="!form.formaPagamento || !form.devolucao_data || sucesso" ><i class="mdi mdi-content-save font-size-18"></i> Salvar </button>                        
                    </div>
                </form><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>

        <div class="modal fade" id="modal-cancelar" tabindex="-1" aria-labelledby="modal-cancelar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-dialog-scrollable modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-cancelarTitle">  Deseja realmente voltar esta Os para Andamento?</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <p v-if="!sucessoCancelar">Tem certeza que deseja retornar a Ordem <strong> {{form.id}} </strong> ?</p>
                        <p>Esta ação cancelar lançamentos financeiros!</p>
                        
                        <div v-if="sucessoCancelar" class="text-center" >
                            <div class="swal2-icon swal2-success swal2-icon-show" style="display: flex;">
                                <div class="swal2-success-circular-line-left" style="background-color: rgb(255, 255, 255);"></div>
                                <span class="swal2-success-line-tip"></span> <span class="swal2-success-line-long"></span>
                                <div class="swal2-success-ring"></div> <div class="swal2-success-fix" style="background-color: rgb(255, 255, 255);"></div>
                                <div class="swal2-success-circular-line-right" style="background-color: rgb(255, 255, 255);"></div>
                            </div>
                            <p class="success text-center font-size-18">{{sucessoCancelar}}</p>
                            
                        </div>
                        <div class="row">
                            <label class="col-sm-3 col-form-label">Observações</label>
                            <div class="col-sm-9">
                                <container-componente :validator="$v.observacoes">
                                    <textarea class="form-control" rows="4" placeholder="Observações sobre a alteração de status" 
                                        v-model="observacoes" @blur="$v.observacoes.$touch()"
                                    ></textarea>
                                </container-componente>
                            </div>
                        </div>

                        
                        <mensagem-componente titulo="Erro!" v-if="erroCancelar" :erros="erroCancelar" @fechar="erroCancelar = null"></mensagem-componente>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoCancelar" @click.prevent="cancelar()"><i class="mdi mdi-trash-can font-size-18"></i> Cancelar </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>
    </div>
</template>

<script>

import { required, requiredIf, minValue, maxLength, email } from "vuelidate/lib/validators";
import { FormaPagamentoService, OrdemServicoService, ParceiroService, PlanoContasService, ServicoItemService } from '../../services'
// import { validarCnpj } from "../../../herpers/helpers";
import moment from 'moment';


const parceiroService = new ParceiroService();
// const tipoItemOsService = new TipoItemOsService();
const ordemServicoService = new OrdemServicoService();

const planoContasService = new PlanoContasService();
const formasPagamentoService = new FormaPagamentoService();
const servicoItemService = new ServicoItemService()

export default {
    data() {
        return {
            form: {
                id: null,
                parceiro_id: '',
                servicos: [],
                caixa: null,
                caixa_id: '',
                devolucao_data: null,
                forma_pagamento_id: ''
            },
            novo_status: '',
            observacoes: '',
            tipos_item: [],
            
            parceiros: [],
            
            buscandoParceiros: false,
            parceiroModal: null,
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
            timer: null,

            contas: [],
            buscandoContas: false,

            erroModal: null,
            sucessoModal:null,
            observacoes_avulsas: '',

            preco:{
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            },


            contas: [],
            formasPagamento: [],
            sucessoCancelar: null,
            erroCancelar: null,
            servicos_item: []
        }
    
    },
    validations() {
        return {

            observacoes: {required }, //: requiredIf(function(){ return this.novo_status == 'ENTREGUE' || this.novo_status == 'CANCELADO' || (this.novo_status == 'ANDAMENTO' && this.form.status == 'PENDENTE') }) },

            parceiroModal: {
                apelido: { required, maxLength:maxLength(50)},
                razao_social: { required, maxLength:maxLength(200)},
                nome_fantasia: { required: requiredIf(function(){ return this.form.tipo_pessoa == 'JURIDICA' && !this.form.estrangeiro }), maxLength:maxLength(200)},
                tipo_pessoa: { required, maxLength:maxLength(20)},
                tipo_documento: {maxLength:maxLength(20)}, //{ required: requiredIf(function(){ return this.form.estrangeiro }), maxLength:maxLength(20)},
                documento: {maxLength:maxLength(20)}, //{ required, maxLength:maxLength(20), validarCpfCnpj: validarCpfCnpj(this.form.tipo_pessoa, this.form.estrangeiro)},
                rg_ie: { maxLength:maxLength(20)},
                rg_ie_estado: { maxLength:maxLength(2)},  //required: requiredIf(function(){ return this.form.tipo_pessoa == 'FISICA' && this.form.rg_ie.length > 0 && !this.form.estrangeiro }), maxLength:maxLength(2)},
                im: { maxLength:maxLength(20)},
                
                ativo: { required },
                cliente: { required },
                fornecedor: { required },
                cidade_id: { },
                endereco_tipo: { maxLength:maxLength(20)},
                logradouro: { maxLength:maxLength(100)},
                numero: { maxLength:maxLength(10)},
                complemento: { maxLength:maxLength(100)},
                bairro: { maxLength:maxLength(100)},
                cep: {  maxLength:maxLength(10)}, //required: requiredIf(function(){ return !this.form.estrangeiro }),
                fone: { maxLength:maxLength(20)},
                email: { email, maxLength:maxLength(150)},
            },

            form: {
                parceiro_id: {required},
                descricao: {required},
                // tipo_item_os: {required},
                // marca: {required},
                // modelo: {required},
                
                // identificacao: {required},

                data_entrada: {},
                acessorios: {},
                avarias: {},
                observacoes: {},

                status: {},

                aprovacao_data: {},
                aprovacao_observacoes: {},
                    
                devolucao_data:{required: requiredIf(function(){ return this.form.forma_pagamento_id })},
                devolucao_observacoes: {},
                devolucao_usuario_id: {},

                valor: {},
                parcelas: [],
                fotos_entrada: {},
                fotos_saida: {},
                

                servicos: {
                    required,
                    $each: {
                        descricao: {required},
                        valor: {minValue: minValue(0)},
                        aprovado: {},
                    }
                }                
                
            },
        }
        

    },
    
    methods: {

        submit: function(aviso = true){

            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            this.form.valor_subtotal = this.valor_total;
            this.form.valor_total = this.valor_total;
            let obj = {...this.form};
            obj.servicos = [...this.form.servicos]
            let request = null;
            if(obj.id)    
                request = ordemServicoService.atualizar(obj);
            else
                request = ordemServicoService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    
                    if(aviso){
                        this.sucesso = "Registro salvo com sucesso!";
                    }
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }else{
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => {
                if(aviso){
                    this.erros = erro 
                }else
                    console.log(erro, erro.response)
            })

        },

       

        abrirModalStatus: function(){
            this.sucessoModal = null;
            this.erroModal = null;
            this.novo_status = '';
            this.observacoes = '';
            this.$v.observacoes.$reset();
        },

        atualizarStatus: function(){
            
            this.sucessoModal = null;
            this.erroModal = null;

            if(this.$v.observacoes.$invalid){
                this.erroModal = "É necessário preencher o campo de observações!";
                this.$v.observacoes.$touch();
                return false;
            }

            let obj = {
                id: this.form.id,
                status: this.novo_status,
                observacoes: this.observacoes
            };

            ordemServicoService.atualizarStatus(obj).then(response => {
                this.form.status = this.novo_status;
                this.novo_status = '';
                this.observacoes = '';
                this.sucessoModal = 'Status atualizado com sucesso!!!';
                this.$v.observacoes.$reset();
                this.form.observacoes = response.data.observacoes;
            }).catch( error => {
                this.erroModal = error;
            });
            
        },

        podeTrocarStatus: function(novo_status){
            let status_nao_permitidos = [];
            if(this.form.status == novo_status)
                return false
            switch(this.form.status){
                case 'PENDENTE' :
                    status_nao_permitidos = ['AGUARDANDO PECA','AGUARDANDO RETIRADA','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ORCAMENTO' :
                    status_nao_permitidos = ['AGUARDANDO RETIRADA','ENTREGUE','AGUARDANDO PECA'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'CANCELADO' :
                    status_nao_permitidos = ['AGUARDANDO RETIRADA','ORCAMENTO','AGUARDANDO PECA','ANDAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'AGUARDANDO PECA' :
                    status_nao_permitidos = ['PENDENTE','CANCELADO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ANDAMENTO' :
                    status_nao_permitidos = ['PENDENTE','ORCAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'AGUARDANDO RETIRADA' :
                    status_nao_permitidos = ['PENDENTE','ANDAMENTO','ORCAMENTO','AGUARDANDO PECA','ANDAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ENTREGUE' :
                    status_nao_permitidos = ['PENDENTE', 'ORCAMENTO', 'CANCELADO','AGUARDANDO PECA', 'ANDAMENTO', 'AGUARDANDO RETIRADA', 'ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
            }
        },

        novaObservacaoAvulsa: function(){
            let obj = {
                id: this.form.id,
                observacoes: this.observacoes_avulsas
            }
            ordemServicoService.observacoes(obj).then( response => {
                this.form.observacoes = response.data.observacoes;
                this.observacoes_avulsas = '';
                Vue.$toast.success(`Observação registrada com sucesso!`)
            }).catch( error => {
                console.log(error, error.response);
                Vue.$toast.error(`Não foi possível registrar esta observação!`)
            })

        },

        
        adicionarServico: function(){     
            let servico = {
                        valor: 0,
                        queixa: ''
                    };

            if(this.editando)
                servico.ordem_servico_id = this.form.id
            setTimeout(() => {
                this.form.servicos.push(
                    servico
                );    
            }, 200); 
        },


        atualizarServico: function(indice){
            this.form.servicos[indice].erros = null;
            this.form.servicos[indice].sucesso = null;

            let obj = {...this.form.servicos[indice]};
            obj.valor = obj.novo_valor;
            if(obj.aprovado != obj.novo_aprovado)
                obj.aprovado_em = moment()
            obj.aprovado = obj.novo_aprovado;
            
            
            ordemServicoService.atualizarServico(obj).then(response => {
                this.form.servicos[indice].sucesso = "Serviço atualizado com Sucesso!!!";
                this.form.servicos[indice].valor = this.form.servicos[indice].novo_valor;
                this.form.servicos[indice].aprovado = this.form.servicos[indice].novo_aprovado;
                this.form.servicos[indice].aprovado_em = response.data.aprovado_em;

                this.submit(false);
            }).catch( error => {
                this.form.servicos[indice].erros = error;
            }).finally( () => this.$forceUpdate())
                
                
            
        },

        qtde: function(decimais){
            return {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: decimais,
                masked: false
            }
        },

        buscarObjeto: function(id){
            ordemServicoService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    let i = this.tipos_item.map(x => x.id).indexOf(this.form.tipo_item_os_id);
                    this.form.tipo_item_os = this.tipos_item[i];
                    this.form.servicos = this.form.servicos.map(x => {
                        x.novo_valor = Number(x.valor);
                        x.valor = Number(x.valor);
                        x.novo_aprovado = x.aprovado;
                        x.erros = null;
                        x.sucesso = null;
                        return x;
                    });

                    this.buscarTabelaServicosCliente();
                    
                }).catch( erro => this.erros = erro)
        },

        buscarParceirosDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarParceiros(text, loading)
            }, 700);
        },

        buscarParceiros: function(text, loading){
            this.buscandoParceiros = true;
            parceiroService.listar({texto: text, per_page: 50, id: this.form.id, tipo: 'CLIENTE'})
                .then( response =>  this.parceiros = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoParceiros = false)
        },
       
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },


        novoParceiro: function(){
            this.sucessoModal = null;
            this.erroModal = null;
            this.parceiroModal = {
                ativo: true,
                cliente: true,
                fornecedor: false,
                tipo_pessoa: 'FISICA',
                estrangeiro: false
            }
        },
        
        salvarParceiro: function(){
            this.$v.parceiroModal.$touch();
             this.sucessoModal = null;
            this.erroModal = null;

            if (this.$v.parceiroModal.$invalid){
                this.erroModal = this.$v.parceiroModal;
                return false;
            }
            let obj = {...this.parceiroModal};
            
            parceiroService.criar(obj).then(
                response => {                    
                    this.parceiros = [response.data];
                    this.parceiro = this.parceiros[0];
                    this.form.parceiro_id = this.form.parceiro.id;
                    this.sucessoModal = "Cliente salvo com sucesso!";
                    
                }   
            ).catch( erro => this.erroModal = erro )
        },

        // buscarTiposItem: function(){
            
        //     tipoItemOsService.listar({per_page: 2000, })
        //         .then( response =>  {
        //             this.tipos_item = response.data.data;
        //             if (this.$route.params.id > 0) {
        //                 this.buscarObjeto(this.$route.params.id);
        //             }

        //         } )
        //         .catch( error => console.log(error))
        // },

        imprimir:function(tipo){
            switch(tipo){
                case 'entrada': 
                    return ordemServicoService.imprimirEntrada(this.form.id)
                case 'entrega': 
                    return ordemServicoService.imprimirEntrega(this.form.id)
                case 'orcamento': 
                    return ordemServicoService.imprimirOrcamento(this.form.id)
                case 'laudo': 
                    return ordemServicoService.imprimirLaudo(this.form.id)
            }

        },

        // Área da Finalização da OS

        abrirModalFinalizar: function(){
            if(this.contas.length == 0)
                setTimeout(() => {
                    this.buscarContas();
                }, 150);


            if(this.formasPagamento.length == 0)
                setTimeout(() => {
                    this.buscarFormasPagamento();
                }, 50);
            
            this.form.parcelas = [];
            this.erros = null;
            this.successo = null;
            this.form.devolucao_data = new Date();
        },

        buscarFormasPagamento: function(){
            formasPagamentoService.listar({ativo: true, per_page:1000}).then( response => this.formasPagamento = response.data.data);
        },

        buscarContas:function(){            
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {id: x.id, label: `${x.codigo} | ${x.nome}`}
                    });
                    
            }).finally( () => this.buscandoContas = false)
        },

        selecionarFormaPagamento: function(forma){
            
            this.form.formaPagamento = forma;
            this.form.forma_pagamento_id = forma.id;

            if(forma.caixa){
                this.form.caixa = forma.caixa;
                this.form.caixa_id = forma.caixa.id;
            }else{
                delete this.form.caixa ; //= null;
                delete this.form.caixa_id ; //= '';
            }

            this.gerarParcelas();
            
        },

        gerarParcelas: function(){
            if(!this.form.formaPagamento.vista || !this.form.formaPagamento.compensa_automatico || this.form.formaPagamento.dias > 0){
                if(!this.form.qtde_parcelas)
                    this.form.qtde_parcelas = 1;

                let valor = Number((this.valor_total / this.form.qtde_parcelas).toFixed(2));
                let soma_valor = 0;
                
                this.form.parcelas = [];                
                let vencimento = moment().add(this.form.formaPagamento.dias, 'days');
                for (let i = 1; i <= this.form.qtde_parcelas; i++){

                    this.form.parcelas.push(
                        {
                            parcela: this.form.formaPagamento.vista || this.form.formaPagamento.entrada ? i-1 : i,
                            parcelas: this.form.qtde_parcelas,
                            valor: i != this.form.qtde_parcelas ? valor : Number((this.valor_total - soma_valor).toFixed(2)),
                            data_vencimento: vencimento.toDate()
                        }  
                    );
                    soma_valor += valor;
                    vencimento = vencimento.add(1, 'months');
                }
                

            }else{
                this.form.qtde_parcelas = 0;
                this.form.parcelas = null;
            }

            this.$forceUpdate();
            

        },

        finalizar: function(){
            this.erros = null;
            this.successo = null;
            this.form.valor_subtotal = this.valor_total;
            this.form.valor_total = this.valor_total;
            ordemServicoService.entregarEquipamento(this.form).then(response => {
                this.form.valor_subtotal = response.data.valor_subtotal;
                this.form.valor_acrescimo = response.data.valor_acrescimo;
                this.form.valor_desconto = response.data.valor_desconto;
                this.form.valor_total = response.data.valor_total;
                this.form.status = response.data.status;
                this.form.devolucao_data = response.data.devolucao_data;
                this.form.devolucao_observacoes = response.data.devolucao_observacoes;
                this.form.devolucao_usuario_id = response.data.devolucao_usuario_id;
                
                this.sucesso = "Operação realizada com sucesso!";
            }).catch(
                error => this.erros = error
            )
        },

        buscarContasDebounce: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarContas();
            }, 800);
        },
        
        buscarContas:function(){            
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {id: x.id, label: `${x.codigo} | ${x.nome}`}
                    });
                    
            }).finally( () => this.buscandoContas = false)
        },

        cancelar: function(){
           
            this.erroCancelar = null;
            this.sucessoCancelar = null;    
            this.errosTitulo ='';
            if(this.$v.observacoes.$invalid){
                this.erroModal = "É necessário preencher o campo de observações!";
                this.$v.observacoes.$touch();
                return false;
            }
            let obj = {id: this.form.id, observacoes: this.observacoes};
            
            let request = ordemServicoService.cancelar(obj);

            request.then(
                response => {
                    //Data de Entrega
                    this.form.status = response.data.status;
                    this.sucessoCancelar = "OS está em andamento novamente!";
                    
                }   
            ).catch( erro => this.erroCancelar = erro )
        },

        buscarServicosItem: function(){
            servicoItemService.listar({ativo: 1, per_page:1000}).then( response => {
                this.servicos_item = response.data.data.map(x => {
                    x.valor_unitario = Number(x.valor_unitario);
                    return x;
                });
            }).catch(error => {
                console.log('Erro ao buscar Serviços Itens: ', error, error.response);
                setTimeout(() => {
                    this.buscarServicosItem();
                }, 2000);
            })
        },

        selecionouServico:function(servico){
            setTimeout(() => {
                servico.servico_item = {...this.servicos_item[this.servicos_item.map(x => x.id).indexOf(servico.servico_item_id)]};
                let valor_unitario = servico.servico_item.valor_unitario;
                if(this.cliente_tem_tabela(servico)){
                    
                    valor_unitario = this.valor_unitario_cliente(servico);
                    servico.valor_unitario = valor_unitario;
                    servico.valor_selecionado = 'Cliente';
                    servico.servico_item.valor_unitario_cliente = valor_unitario;
                    // servico.valor = valor_unitario;
                    servico.novo_valor = valor_unitario
                }else{
                    servico.valor_selecionado = 'Tabela';
                    servico.valor_unitario = valor_unitario;
                    servico.servico_item.valor_unitario_cliente = 0;
                    // servico.valor = valor_unitario;
                    servico.novo_valor = valor_unitario
                }                
                
            }, 10);
        },

        cliente_tem_tabela: function(servico){
            
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return false;

            let i = this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id) 
            if(i < 0)
                return false
            let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id)];
            return servico_cliente.pivot.valor_unitario > 0
        },
        valor_unitario_cliente: function(servico){
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return -1;

             let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id)];
             
             if(servico_cliente.pivot.valor_unitario > 0){
                return servico_cliente.pivot.valor_unitario
             }
                
            return servico_cliente.valor_unitario
        },

        buscarTabelaServicosCliente: function(){
            setTimeout(() => {
                parceiroService.buscarTabelaServicos(this.form.parceiro_id).then( response =>{
                    this.form.parceiro.tabela_servicos = response.data.map(x => {
                        x.pivot.valor_unitario = Number(x.pivot.valor_unitario)
                        x.valor_unitario = Number(x.valor_unitario)
                        return x
                    })
                })    
            }, 800);
            
        },

    },
    mounted() {

        if (this.$route.params.id > 0) {
            setTimeout(() => {
                this.buscarObjeto(this.$route.params.id);
            }, 500);
        }

        setTimeout(() => {
            this.buscarServicosItem();
        }, 50);

        
    },
    computed:{
        editando(){
            return this.$route.params.id > 0;
        },
        podeAdicionarServico(){
            return !this.editando || true;
        },
        podeEditar(){
            // 'AGUARDANDO PECA','ANDAMENTO'
            let x = ['AGUARDANDO RETIRADA','ENTREGUE','CANCELADO'];
            return x.indexOf(this.form.status) < 0
        },

        valor_total(){
            let v = this.form.servicos.filter(x => x.aprovado == 'APROVADO').reduce((a,c) => Number(a) + Number(c.valor), 0)
            this.form.valor_subtotal = v;
            this.form.valor_total = v;
            return v;
        },

        valor_total_orcamento(){
            return this.form.servicos.reduce((a,c) => Number(a) + Number(c.valor), 0)

        }
    }

   
};
</script>
